#menu-header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 50px 0;
  .logo svg {
    position: relative;
    z-index: 2;
    width: 200px; }
  .items {
    a {
      margin-left: 15px;
      margin-right: 15px; } }
  .bag svg {
    width: 20px; } }

#menu-left {
  width: 300px;
  height: 100vh;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background: pink;
  .ham {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    .line {
      display: inline-block;
      width: 2px;
      height: 30px;
      margin-right: 2px;
      background: $black;
      position: relative; } }
  .click .line {
    &:first-child {
      transform: rotate(45deg);
      left: 7px; }
    &:last-child {
      transform: rotate(-45deg); } } }
